import { Component, ViewChild, ElementRef, AfterViewInit, Directive, Input, Renderer, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as global from '../globals';

@Component({
  selector: 'app-infrastructure',
  templateUrl: './infrastructure.component.html',
  styleUrls: ['./infrastructure.component.css']
})

export class InfrastructureComponent implements OnInit, AfterViewInit {
  map: ol.Map;
  source = new ol.source.Vector({ wrapX: false });
  selectTool;
  modifyTool;
  vector = new ol.layer.Vector();
  formInfrastructur: FormGroup;
  objectfunctions = [
    {
      'value':'Pipe gas',
      'text':'Pipe gas'
    },
    {
      'value': 'Fire department',
      'text':'Fire department'
    },
    {
      'value': 'Generator',
      'text':'Generator'
    },
    {
      'value' : 'Culvert',
      'text' : 'Culvert'
    }
  ]
   
  constructor(fb: FormBuilder) {
    this.formInfrastructur = fb.group({
      'name': [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'startdate': '',
      'enddate': '',
      'status': '',
      'type': '',
      'function': '',
      'description': [null]
    })

  }

  ngAfterViewInit() {
this.vector.setSource(this.source);
    this.map = new ol.Map({
      layers: [
        new ol.layer.Tile({
          source: new ol.source.OSM()
        })
      ],
      target: 'map',
      controls: ol.control.defaults({
        attributionOptions: /** @type {olx.control.AttributionOptions} */ ({
          collapsible: false
        })
      }),
      view: new ol.View({
        projection: "EPSG:4326",
        center: [112.755946, -7.3310086],
        zoom: 15
      })
    });

    this.map.addLayer(this.vector);

    let map = this.map;
    $("#tabform").on('shown.bs.tab', function () {
      map.updateSize();
    });

    $('[data-toggle="tooltip"]').tooltip({container: 'body'});

  }

  draw(type) {

    this.source.clear();

    let draw = new ol.interaction.Draw({
      source: this.source,
      type: type
      
    });

    this.map.addInteraction(draw);

    let map = this.map;
    draw.on('drawend', function (e) {
      map.removeInteraction(draw);
      let format = new ol.format.WKT();
      let wktgeometry = format.writeFeature(e.feature);
      let extent = e.feature.getGeometry().getExtent();
      let X = extent[0] + (extent[2] - extent[0]) / 2;
      let Y = extent[1] + (extent[3] - extent[1]) / 2;
      $("#geometry").val(wktgeometry);
      $("#x").val(X);
      $("#y").val(Y);
    });
  }

  modify() {
    this.selectTool = new ol.interaction.Select({
      wrapX: false,
    });

    this.modifyTool = new ol.interaction.Modify({
      features: this.selectTool.getFeatures()
    });

    this.map.addInteraction(this.selectTool);
    this.map.addInteraction(this.modifyTool);

  }

  deactive() {
    this.map.removeInteraction(this.selectTool);
    this.map.removeInteraction(this.modifyTool);

    let format = new ol.format.WKT();
    let wktgeometry = format.writeFeature(this.source.getFeatures[0]);
    $("textarea[name='geometry']").val(wktgeometry)
  }

  reset() {
    this.source.clear();
    $("textarea[name='geometry']").val("")
  }

  ngOnInit() {


  }

}



