import { Injectable } from "@angular/core";
import { Router } from '@angular/router';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
// import { AuthService } from '../auth/auth.service';
import { Observable } from 'rxjs/Rx';
import * as globals from '../globals';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class BuildingService {

  data: any = {};

  constructor(private http: Http) { }


  private resetPropertyObject() {
    this.data = {};
    this.data.userid = localStorage.getItem(globals.appID + "User");
    this.data.token = localStorage.getItem(globals.appID + "Token");
  }

  save(paramsData: string): Promise<globals.ResponseModel> {

    return this.http.post(globals.apiUrl + "building/add", paramsData, globals.httpHeader)
      .toPromise().then(response => response.json() as globals.ResponseModel);
  }

  detail(id: number): Promise<globals.ResponseModel> {
    this.resetPropertyObject();

    return this.http.post(globals.apiUrl + "building/detail/" + id, $.param(this.data), globals.httpHeader)
      .toPromise().then(response => response.json() as globals.ResponseModel);

  }

  getData(page: number = 1, rows: number = 10, filterstatus?: string): Promise<globals.ResponseModel> {
    this.resetPropertyObject();
    this.data.page = page;
    this.data.rows = rows;
    this.data.status = filterstatus;

    return this.http.post(globals.apiUrl + "building/get", $.param(this.data), globals.httpHeader)
      .toPromise().then(response => response.json() as globals.ResponseModel)
  }

  getMap(minX: number, maxX: number, minY: number, maxY: number): Promise<globals.ResponseModel> {

    let data = {
      minX: minX,
      maxX: maxX,
      minY: minY,
      maxY: maxY
    }

    return this.http.post(globals.apiUrl + "building/getmap", $.param(data), globals.httpHeader)
      .toPromise().then(response => response.json() as globals.ResponseModel);
  }

  update(paramsData:Object): Promise<globals.ResponseModel>{
        this.resetPropertyObject();
        
        return this.http.post(globals.apiUrl + "building/update", paramsData + "&" + $.param(this.data), globals.httpHeader)
            .toPromise().then( response => response.json() as globals.ResponseModel );
    }

}



