import { Component, ViewChild, ElementRef, AfterViewInit, Directive, Input, Renderer, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CompanyService } from './company.service';
import * as global from '../globals';


@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.css']
})
export class CompanyComponent implements OnInit, AfterViewInit {

  companies: Object[] = global.companies;
  formCompany: FormGroup;

  constructor(fb: FormBuilder, private svc: CompanyService) {

    this.formCompany = fb.group({
      'title': [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'id': '',
      'startyear': '',
      'endyear': '',
      'category': '',
      'postcode': '',
      'address': '',
      'city': '',
      'number': '',
      'building': '',
      'x': '',
      'y': '',
      'description': [null]
    })


  }

  ngAfterViewInit() {
    $('[data-toggle="tooltip"]').tooltip({container: "body"});

  }

  ngOnInit() {
      $(".my-group-button").css("height", "+=1px");
  }

  saveCompany(event: Event) {

    event.preventDefault();

    if (this.formCompany.valid) {

      let formData = $("#formCompany").serialize();

      this.svc.save(formData).then(response => {
        if (response.status == true) {
          //swal("Done!", global.SuccessSaveMessage, "success");
          alert("Save successfully!");
        }
        else {
          alert("Failed!");
          // swal("Failed!", response.message, "error");
        }

      });

    }

  }

}



