import {Component, Input, Directive} from '@angular/core'
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import * as globals from '../globals';

@Component({
    selector: "form-input",
    template:`
     <div class="form-group  has-feedback" [ngClass]="{'has-error': !control.valid && control.dirty, 'has-success': control.valid && control.dirty}">
            <label class="control-label">{{labelText}}</label>
            <input *ngIf="type != 'textarea' && type != 'select'" type="{{type}}" class="form-control" id="{{inputName}}" name="{{inputName}}" placeholder="{{placeHolder}}" [formControl]="control"> 
            <textarea *ngIf="type == 'textarea'" class="form-control" id="{{inputName}}" name="{{inputName}}" placeholder="{{placeHolder}}" [formControl]="control"></textarea>
            <select *ngIf="type == 'select'" class="form-control" id="{{inputName}}" name="{{inputName}}"  [formControl]="control">
                <option *ngFor="let item of list" value="{{item.value}}">{{item.text}}</option> 
            </select>
            <span *ngIf=" control.valid && control.dirty" class="glyphicon glyphicon-ok form-control-feedback" aria-hidden="true"></span>
            <span *ngIf="!control.valid && control.dirty" class="glyphicon glyphicon-remove form-control-feedback" aria-hidden="true"></span>
        </div>
    `
})


export class FormInputComponent {
    @Input() type:string='text';
    @Input() placeHolder:string='';
    @Input() labelText:string = '';
    @Input() inputName:string='';
    @Input() control: FormControl;
    @Input() list: globals.ISelectOption[];
}




