import { Component, ViewChild, ElementRef, AfterViewInit, Directive, Input, Renderer, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as global from '../globals';
import { BuildingService } from './building.service';

@Component({
  selector: 'app-building',
  templateUrl: './building.component.html',
  styleUrls: ['./building.component.css']
})
export class BuildingComponent implements OnInit, AfterViewInit {

  map: ol.Map;
  source = new ol.source.Vector({ wrapX: false });
  selectTool;
  modifyTool;
  vector = new ol.layer.Vector();
  currentPage: number = 1;
  rows: number = global.totalRowDataPagination;
  total: number;
  buildings: Object[] = global.buildings;
  formBuilding: FormGroup;

  constructor(fb: FormBuilder, private svcBuilding: BuildingService) {
    this.formBuilding = fb.group({
      'title': [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      'id': '',
      'year': '',
      'function': '',
      'status': '',
      'postcode': '',
      'address': '',
      'city': '',
      'number': '',
      'geometry': '',
      'x': '',
      'y': '',
      'description': [null]
    })
  }

  ngOnInit() {
    //this.getPage(this.currentPage);
    let detach = $("#modalBuilding").detach();
    $("body").append(detach);
  }

  ngAfterViewInit() {

    this.vector.setSource(this.source);
    this.map = new ol.Map({
      layers: [
        new ol.layer.Tile({
          source: new ol.source.OSM()
        })
      ],
      target: 'map',
      controls: ol.control.defaults({
        attributionOptions: /** @type {olx.control.AttributionOptions} */ ({
          collapsible: false
        })
      }),
      view: new ol.View({
        projection: "EPSG:4326",
        center: [112.755946, -7.3310086],
        zoom: 15
      })
    });

    this.map.addLayer(this.vector);

    let map = this.map;
    $("#tabform").on('shown.bs.tab', function () {
      map.updateSize();
    });

    $('[data-toggle="tooltip"]').tooltip({ container: 'body' });

  }


  showCompanyInfo(params:any){
    let html = `<table class="table table-striped table-reponsive">
                    <tbody>
                         <tr>
                             <td>Building ID</td>
                             <td>`+params.id+`</td>
                         </tr> 
                         <tr>
                             <td>Name</td>
                             <td>`+params.title+`</td>
                         </tr> 
                         <tr>
                             <td>Built Year</td>
                             <td>`+params.year+`</td>
                         </tr> 
                         <tr>
                             <td>Function</td>
                             <td>`+params.id+`</td>
                         </tr> 
                         <tr>
                             <td>Address</td>
                             <td>`+params.address+`</td>
                         </tr> 
                         <tr>
                             <td>Number</td>
                             <td>`+params.number+`</td>
                         </tr> 
                         <tr>
                             <td>City</td>
                             <td>`+params.city+`</td>
                         </tr> 
                         <tr>
                             <td>PostCode</td>
                             <td>`+params.postcode+`</td>
                         </tr> 
                         <tr>
                             <td>Description</td>
                             <td>`+params.description+`</td>
                         </tr>       
                    </tbody>
                </table>`;
    $("#modalBuilding div.modal-body").html("").html(html);
    $("#modalBuilding").modal("show");
  }

  showBuildingHistory(params:any){
    console.log(params)
    let rows="";

    params.forEach(function(item){
      rows += `<tr>
                  <td>`+item.id+`</td>
                  <td>`+item.title+`</td>
                  <td>`+item.category+`</td>
                  <td>`+item.startyear+`</td>
                  <td>`+item.endyear+`</td>
               </tr>
              `;
    });

    $("#modalBuildingHistory div.modal-body table tbody").html("").html(rows);
    $("#modalBuildingHistory").modal("show");
  }

  getPage($event) {
    this.currentPage = $event;
    this.svcBuilding.getData(this.currentPage, this.rows).then(
      response => {
        if (response.status == true) {
          this.total = response.data.total;
          this.buildings = response.data;
        }
      }
    )
  }

  saveBuilding(event: Event) {

    event.preventDefault();

    if (this.formBuilding.valid) {

      let formData = $("#formBuilding").serialize();

      this.svcBuilding.save(formData).then(response => {
        if (response.status == true) {
          //swal("Done!", global.SuccessSaveMessage, "success");
          alert("Save successfully!");
          this.formBuilding.reset();
          this.getPage(this.currentPage);
        }
        else {
          alert("Failed!");
          // swal("Failed!", response.message, "error");
        }

      });

    }

  }

  draw() {

    this.source.clear();

    let draw = new ol.interaction.Draw({
      source: this.source,
      type: "Polygon"
    });

    this.map.addInteraction(draw);

    let map = this.map;
    draw.on('drawend', function (e) {
      map.removeInteraction(draw);
      let format = new ol.format.WKT();
      let wktgeometry = format.writeFeature(e.feature);
      let extent = e.feature.getGeometry().getExtent();
      let X = extent[0] + (extent[2] - extent[0]) / 2;
      let Y = extent[1] + (extent[3] - extent[1]) / 2;
      $("#geometry").val(wktgeometry);
      $("#x").val(X);
      $("#y").val(Y);
    });
  }

  modify() {
    this.selectTool = new ol.interaction.Select({
      wrapX: false,
    });

    this.modifyTool = new ol.interaction.Modify({
      features: this.selectTool.getFeatures()
    });

    this.map.addInteraction(this.selectTool);
    this.map.addInteraction(this.modifyTool);

  }

  deactive() {
    this.map.removeInteraction(this.selectTool);
    this.map.removeInteraction(this.modifyTool);

    let format = new ol.format.WKT();
    let wktgeometry = format.writeFeature(this.source.getFeatures[0]);
    $("textarea[name='geometry']").val(wktgeometry)
  }

  reset() {
    this.source.clear();
    $("textarea[name='geometry']").val("")
  }

}



